import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
export default {
	components:{
		uniSwipeAction
	},
	data() {
		return {
			title: 'Hello',
			address:null,
			bill:false,
			options2: [{
					text: '编辑',
					style: {
						backgroundColor: '#007AFF'
					}
			},{
				text: '删除',
				style: {
					backgroundColor: '#dd524d'
				}}
				],
		}
	},
	onLoad(option) {
		if(option.bill){
			this.bill = true
		}
		this.getAddress();
	},
	methods: {
		bindClick(e,s){
			let _this = this;
			console.log(e)
			if(e.content.text == '编辑'){
				uni.setStorageSync('address',JSON.stringify(s));
				uni.navigateTo({
					"url":"/pages/mine/eaddress"
				})
			}else{
				uni.showModal({
					"content":"删除确认",
					"title":"确定要删除此地址吗？",
					"success":function(res){
						if(res.confirm){
							_this.$ajax("/useraddress/remove","POST",{id:s.id}).then(res1=>{
								
								if(res){
									uni.showToast({
										"title":"地址已经删除",
										"icon":"none"
									})
									_this.getAddress();
								}
								
							})
						}
					}
				})
			}
			// uni.showToast({
			// 	title: `点击了${e.content.text}按钮`,
			// 	icon: 'none'
			// })
		},
		picker(s,i){
			
		},
		aaddress(){
			uni.navigateTo({
				"url":"/pages/mine/aaddress"
			})
		},
		getAddress(){
			let _this = this;
			this.$ajax("/useraddress/page","POST",{pageNo:1,pageSize:10}).then((res)=>{
				_this.address = res.list;
				_this.address.map((res1,index1)=>{
					if(_this.address[index1].defaultFlag == 0){
						_this.address[index1].defaultFlag = false;
					}else{
						_this.address[index1].defaultFlag = true;
					}
					
				})
				console.log(_this.address);
				
			})
		},
		setDefalut(s,i){
			console.log(s);
			let _this = this;
			// this.address.map((s,i)=>{
			// 	_this.address[i].c = false;
			// })
			this.$ajax("/useraddress/setDefault","POST",{id:s.id}).then((res)=>{
				if(res){
					_this.getAddress();
					uni.showToast({
						title:'设置成功',
						"icon":"none"
					});
					if(_this.bill){
						uni.navigateBack({
							delta:1
						})
					}
					
					
				}else{
					
				}
			})
			
			
			
		}
	}
}
